import React, { Component } from 'react';
import Dropzone from 'react-dropzone'

const FORM_FIELDS = [
    {
        name: "name",
        type: "text",
        label: "Partner Name",
        error: false,
        background: null
    },
    {
        name: "code",
        type: "text",
        label: "Partner ID (unlock code)",
        error: false,
        background: null
    },
    {
        name: "tint_color",
        type: "color",
        label: "Tint Color",
        error: false,
        background: null
    },
    {
        name: "contrast_tint_color",
        type: "color",
        label: "Contrast Tint Color",
        error: false,
        background: null
    },
    {
        name: "primary_color",
        type: "color",
        label: "Primary Color",
        error: false,
        background: null
    },
    {
        name: "secondary_color",
        type: "color",
        label: "Secondary Color",
        error: false,
        background: null
    },
    {
        name: "accept_color",
        type: "color",
        label: "Accept (success) Color",
        error: false,
        background: null
    },
    {
        name: "error_color",
        type: "color",
        label: "Error Color",
        error: false,
        background: null
    },
    {
        name: "id",
        type: "text",
        label: "Internal ID (autogenerated)",
        error: false,
        background: null
    },
]
const steps = {
    LOGO: "logo",
    SCREEN_IMAGE: "screenImage",
    SCREEN_WIDE_IMAGE: "screenWideImage",
    DATA: "data"
}
class PartnerForm extends Component {
    constructor(props) {
        super(props);

        this.state = {
            fields: FORM_FIELDS,
            step: steps.LOGO,
            data: {},
            logoDataURL: null,
            logoFile: null,
            screenImageDataURL: null,
            screenImageFile: null,
            screenWideImageDataURL: null,
            screenWideImageFile: null
        };

        this.handleSubmit = this.handleSubmit.bind(this);
        this.logoFileDropped = this.logoFileDropped.bind(this);
        this.screenImageFileDropped = this.screenImageFileDropped.bind(this);
        this.screenWideImageFileDropped = this.screenWideImageFileDropped.bind(this);
    }

    hasErrors() {
        console.log("Checking for errors... " + JSON.stringify(this.state.fields, null, 2));
        for (var field of this.state.fields) {
            if (field.error) return true
        }
        return false
    }

    handleSubmit(event) {
        event.preventDefault();

        if (this.hasErrors()) {
            alert("Not so fast! There are some errors, please check the data...");
            return;
        }

        var formData = new FormData(event.target);
        var object = {};
        formData.forEach((value, key) => {
            if (value !== null && value !== "") {
                object[key] = value;
            }
        });
        var files = {
            logo: this.state.logoFile,
            screenImage: this.state.screenImageFile,
            screenWideImage: this.state.screenWideImageFile
        }

        // Notify up state:
        this.props.onSubmit(object, files);
    }

    logoFileDropped(files) {
        var file = files[0];
        var reader = new FileReader();
        // it's onload event and you forgot (parameters)
        reader.onload = (e) => {
            var currentStep = this.state.step;
            this.setState({
                logoDataURL: e.target.result,
                logoFile: file,
                step: currentStep === steps.LOGO ? steps.SCREEN_IMAGE : currentStep
            });
        };
        // you have to declare the file loading
        reader.readAsDataURL(file);
    }

    screenImageFileDropped(files) {
        var file = files[0];
        var reader = new FileReader();
        // it's onload event and you forgot (parameters)
        reader.onload = (e) => {
            var currentStep = this.state.step;
            this.setState({
                screenImageDataURL: e.target.result,
                screenImageFile: file,
                step: currentStep === steps.SCREEN_IMAGE ? steps.SCREEN_WIDE_IMAGE : currentStep
            });
        };
        // you have to declare the file loading
        reader.readAsDataURL(file);
    }

    screenWideImageFileDropped(files) {
        var file = files[0];
        var reader = new FileReader();
        // it's onload event and you forgot (parameters)
        reader.onload = (e) => {
            var currentStep = this.state.step;
            this.setState({
                screenWideImageDataURL: e.target.result,
                screenWideImageFile: file,
                step: currentStep === steps.SCREEN_WIDE_IMAGE ? steps.DATA : currentStep
            });
        };
        // you have to declare the file loading
        reader.readAsDataURL(file);
    }

    onDropInvalidFile() {
        alert("Now slow down! This doesn't seem to be a valid image. We only accept PNGs...");
    }

    fieldChangeCallback(field) {
        if (field.type === "color") return (event) => {
            var value = correctColorString(event.target.value);
            var isValid = isValidColorString(value);
            this.setFieldBackgroundColor(field, isValid ? value : null, () => {
                this.setFieldErrorState(field, !isValid && value !== "", () => {
                    this.setState({
                        data: Object.assign({}, this.state.data, {
                            [field.name]: value
                        })
                    })
                });
            });
        };
        else if (field.name === "name") return (event) => {
            // The name field has some side effects:
            // - Autogenerate the unlock code
            // - Autogenerate the internal ID
            var value = event.target.value
            if (value !== "") {
                var strippedValue = lowerCaseAndRemoveSpaces(value)
                this.setState({
                    data: Object.assign({}, this.state.data, {
                        id: strippedValue,
                        code: strippedValue + random4()
                    })
                })
            }
        };
        else return (event) => { };

        function correctColorString(colorString) {
            if (colorString.startsWith("#") && colorString.length === 7) return colorString; // Color starting with #
            else if (!colorString.startsWith("#") && colorString.length === 6) return "#" + colorString; // Color without #
            else return colorString // Not a valid color
        }

        function isValidColorString(colorString) {
            return /^#[0-9A-F]{6}$/i.test(colorString)
        }

        function random4() {
            return Math.floor((Math.random() * 8999) + 1000);
        }
        function lowerCaseAndRemoveSpaces(str) {
            return str
                .replace(/\s+/g, '')
                .toLowerCase();
        }
    }

    setFieldBackgroundColor(field, backgroundColor, callback) {
        console.log("Setting " + field.name + " background color to " + backgroundColor);
        this.setState({
            fields: this.state.fields.map((f) => {
                if (f.name === field.name) {
                    return Object.assign({}, f, { background: backgroundColor });
                } else return f;
            })
        }, callback)
    }
    setFieldErrorState(field, isError, callback) {
        this.setState({
            fields: this.state.fields.map((f) => {
                if (f.name === field.name) {
                    return Object.assign({}, f, { error: isError });
                } else return f;
            })
        }, callback)
    }

    render() {
        var logoPicker = <Dropzone
            key="logoPicker"
            accept={['image/png']}
            multiple={false}
            onDropAccepted={this.logoFileDropped}
            onDropRejected={this.onDropInvalidFile}>
            {({ getRootProps, getInputProps }) => (
                <section>
                    <div {...getRootProps()}>
                        <input {...getInputProps()} />
                        <p>
                            Drop the partner LOGO here (or click to select file)
                    {this.state.logoDataURL &&
                                <img className="logo" alt="Logo"
                                    src={this.state.logoDataURL} />}
                        </p>
                    </div>
                </section>
            )}
        </Dropzone>;

        var screenImagePicker = <Dropzone
            key="screenImagePicker"
            accept={['image/png']}
            multiple={false}
            onDropAccepted={this.screenImageFileDropped}
            onDropRejected={this.onDropInvalidFile}>
            {({ getRootProps, getInputProps }) => (
                <section>
                    <div {...getRootProps()}>
                        <input {...getInputProps()} />
                        <p>
                            Drop the NARROW SCREEN IMAGE here (or click to select file)
                    {this.state.screenImageDataURL &&
                                <img className="screen-image" alt="Screen"
                                    src={this.state.screenImageDataURL} />}
                        </p>
                    </div>
                </section>
            )}
        </Dropzone>;

        var screenWideImagePicker = <Dropzone
            key="screenWideImagePicker"
            accept={['image/png', 'image/jpeg']}
            multiple={false}
            onDropAccepted={this.screenWideImageFileDropped}
            onDropRejected={this.onDropInvalidFile}>
            {({ getRootProps, getInputProps }) => (
                <section>
                    <div {...getRootProps()}>
                        <input {...getInputProps()} />
                        <p>
                            Drop the WIDE SCREEN IMAGE here (or click to select file)
                    {this.state.screenWideImageDataURL &&
                                <img className="screen-image" alt="Screen wide"
                                    src={this.state.screenWideImageDataURL} />}
                        </p>
                    </div>
                </section>
            )}
        </Dropzone>;

        var dataFields = this.state.fields.map((field, index) => {
            console.log("Background: " + field.background);
            var style = {
                backgroundColor: field.background
            };
            if (field.error) {
                style["borderBottom"] = "thick solid #FF0000";
            }
            return <React.Fragment
                key={field.name}>
                <label>{field.label}
                    <input
                        name={field.name}
                        type="text"
                        value={this.state.data[field.name]}
                        onChange={this.fieldChangeCallback(field)}
                        style={style}
                    />
                </label>
            </React.Fragment>
        });

        var submitButton = <button type="submit">Submit demo!</button>;

        var completeView = [logoPicker, screenImagePicker, screenWideImagePicker, dataFields, submitButton];
        return (
            <form onSubmit={this.handleSubmit}>
                {(() => {
                    switch (this.state.step) {
                        case steps.LOGO: return completeView.slice(0, 1);
                        case steps.SCREEN_IMAGE: return completeView.slice(0, 2);
                        case steps.SCREEN_WIDE_IMAGE: return completeView.slice(0, 3);
                        default: return completeView;
                    }
                })()}
            </form>
        );
    }
}
export default PartnerForm;