import React, { Component } from 'react';
import PartnerForm from './PartnerForm.js';
import Utils from './Utils.js';
import GitHubController from './GitHubController.js';

const JSON_URL = "https://raw.githubusercontent.com/mozioinc/sdk_demos/master/demos.json";
class PartnerPage extends Component {
    constructor(props) {
        super(props);
        this.gitHubController = new GitHubController("mozioinc", "sdk_demos", this.props.github);
        this.utils = new Utils();
        this.state = {
            partners: [],
            loading: false
        };

        this.onSubmit = this.onSubmit.bind(this);
    }

    async getJsonPartners() {
        var response = await fetch(JSON_URL);
        var stringResponse = await response.text();
        console.log(response);
        return JSON.parse(atob(stringResponse))
    }

    componentDidMount() {
        this.getJsonPartners()
            .then(data => {
                console.log(data)
                this.setState({
                    partners: data
                })
            });
    }

    onSubmit(data, files) {
        if (!this.validateOrAbort(data)) { return; }

        window.scrollTo(0, 0);
        this.setState({
            loading: true
        });

        // var repo = github.getRepo("mozioinc", "sdk_demos");
        var branchName = "demo-" + data.id;
        var logoImageName = "logo_" + data.id + ".png";
        var screenImageName = data.id + "_narrow.png";
        var screenWideImageName = data.id + "_wide.png";

        new Promise(async (resolve, reject) => {
            resolve([
                {
                    path: "images/" + logoImageName,
                    content: await this.utils.readFileAsBas64String(files.logo),
                }, {
                    path: "images/" + screenImageName,
                    content: await this.utils.readFileAsBas64String(files.screenImage),
                }, {
                    path: "images/" + screenWideImageName,
                    content: await this.utils.readFileAsBas64String(files.screenWideImage)
                }
            ]);
        })
            .then((files) => {
                return this.gitHubController.createImageFileTrees(files, "master");
            })
            .then((treeResult) => {
                return this.gitHubController.commitFromTree(treeResult, "Add image files");
            })
            .then((commitResult) => {
                var branchPlaceholder = "{branch_name}";
                data.logo_url = "https://raw.githubusercontent.com/mozioinc/sdk_demos/" + branchPlaceholder + "/images/" + logoImageName;
                data.screen_image_url = "https://raw.githubusercontent.com/mozioinc/sdk_demos/" + branchPlaceholder + "/images/" + screenImageName;
                data.screen_wide_image_url = "https://raw.githubusercontent.com/mozioinc/sdk_demos/" + branchPlaceholder + "/images/" + screenWideImageName;
                var changed = false
                var newArray = this.state.partners.map(function (partner) {
                    if (partner.id === data.id) {
                        // Found a partner with the same ID. Updating it:
                        changed = true
                        return data
                    } else return partner;
                });
                if (!changed) {
                    // No partner with the same ID, so simply add it:
                    newArray.push(data)
                }
                // We create the JSON string and then encode it to base64 (_very_ rudimentary obfuscation)
                var fileContents = btoa(JSON.stringify(newArray))
                var jsonFile = {
                    content: fileContents,
                    path: "demos.json"
                }
                return this.gitHubController.createJsonFileTree(jsonFile, commitResult);
            })
            .then((result) => {
                return this.gitHubController.commitFromTree(result, "Add JSON file");
            })
            .then((commitResult) => {
                return this.gitHubController.branchFromCommit(commitResult, branchName);
            })
            .then(() => {
                var prTitle = data.name;
                var prBody = "Adds all necessary files for the `" + data.name + "` demo: \n" +
                    "```\n" + JSON.stringify(data, null, 2) + "\n```";
                return this.gitHubController.pullRequestFromBranch(branchName, prTitle, prBody);
            })
            .then((response) => {
                alert("Success!");
                window.open(response.data.html_url);
            }, (error) => {
                alert("Error... " + error);
            }).finally(() => {
                this.setState({
                    loading: false
                });
            })
    }

    validateOrAbort(data) {
        if (!data.id || data.id === "") {
            alert("Please fill in the ID!");
            return false;
        }
        if (!data.tint_color || data.tint_color === "") {
            alert("Please fill in the tint color!");
            return false;
        }
        if (!data.contrast_tint_color || data.contrast_tint_color === "") {
            alert("Please fill in the contrast tint color!");
            return false;
        }
        if (!data.primary_color || data.primary_color === "") {
            alert("Please fill in the primary color!");
            return false;
        }
        return true;
    }

    render() {
        return (
            <React.Fragment>
                <ul className="column">
                    {this.state.partners.map(partner =>
                        <li key={partner.id}>
                            <p>{partner.name}</p>
                            <img className="partner-logo" src={partner.logo_url} alt={partner.name} />
                        </li>
                    )}
                </ul>
                {this.state.loading || <div className="column form">
                    <PartnerForm
                        onSubmit={this.onSubmit}
                    />
                </div>}
                {this.state.loading && <p>Saving...</p>}
            </React.Fragment>
        );
    }
}
export default PartnerPage;