class GitHubController {
    constructor(owner, repo, github) {
        this.owner = owner;
        this.repo = repo;
        this.github = github;
    }

    async createImageFileTrees(files, fromBranchName) {
        return this.github.git.getRef({
            owner: this.owner,
            repo: this.repo,
            ref: "heads/"+fromBranchName
        }).then(async (ref) => {
            var masterSha = ref.data.object.sha;
            var treeObjects = []
            for (const file of files) {
                // First, we create the blob (translation: creating a blog means uploading the file):
                var blob = await this.github.git.createBlob({
                    owner: this.owner,
                    repo: this.repo,
                    content: file.content,
                    encoding: "base64"
                });

                // Then, we map each blob to a tree request object:
                treeObjects.push({
                    path: file.path,
                    sha: blob.data.sha,
                    mode: "100644",
                    type: "blob"
                });
            };

            // And finally, create a tree with all the blobs we just created:
            return this.github.git.createTree({
                owner: this.owner,
                repo: this.repo,
                base_tree: masterSha,
                tree: treeObjects
            }).then((tree) => {
                // Map the response to a "tree result" object that can be used in GitHubController.commitBranchFromTree(..):
                return {
                    parentSha: masterSha,
                    treeSha: tree.data.sha
                }
            });
        });
    }

    createJsonFileTree(jsonFile, baseTree) {
        return this.github.git.createTree({
            owner: this.owner,
            repo: this.repo,
            base_tree: baseTree.sha,
            tree: [{
                path: jsonFile.path,
                content: jsonFile.content,
                mode: "100644",
                type: "blob"
            }]
        }).then((tree) => {
            // Map the response to a "tree result" object that can be used in GitHubController.commitBranchFromTree(..):
            return {
                parentSha: baseTree.sha,
                treeSha: tree.data.sha
            }
        });;
    }

    async commitFromTree(treeResult, commitMessage) {
        console.log("commitBranchFromTree: " + JSON.stringify(treeResult, null, 2));
        return this.github.git.createCommit({
            owner: this.owner,
            repo: this.repo,
            message: commitMessage,
            tree: treeResult.treeSha,
            parents: [treeResult.parentSha]
        }).then((result) => {
            return {
                sha: result.data.sha
            };
            
        });
    }

    async branchFromCommit(commitResult, branchName) {
        return this.github.git.createRef({
            owner: this.owner,
            repo: this.repo,
            ref: "refs/heads/" + branchName,
            sha: commitResult.sha
        });
    }

    async pullRequestFromBranch(branchName, title, body) {
        return this.github.pulls.create({
            owner: this.owner,
            repo: this.repo,
            title: title,
            body: body,
            head: branchName,
            base: "master"
        });
    }
}

export default GitHubController;